import React, { useState, useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import { Table, Card } from 'antd';
import UnderConstruction from '../../../../../components/shared-components/Dropdown/UnderConstruction'
import DisplayDisclosure from '../../../../../components/shared-components/Buttons/DisplayDisclosure'
import data from '../data/RecordsData'

export default function MainTable({ match }) {
  const history = useHistory()
  const [alreadySelectedRows, setAlreadySelectedRows] = useState([])

  useEffect(() => {
    console.log(alreadySelectedRows)
  }, [alreadySelectedRows])

  const columns = [
    {
      title: ' ',
      dataIndex: 'key',
      key: ' ',
      render: (text) => <span>{`${text}#`}</span>
    },
    {
      title: '    ',
      dataIndex: 'sentAt',
      key: '    '
    },
    {
      title: '',
      key: '',
      width: '30%',
      render: () => <DisplayDisclosure width={250} content={' '} onclick={() => history.push('/home/disclosures/internal-controller/records/1')} />
    }
  ]

  return (
    <Card title=" " extra={<UnderConstruction />} bordered={false} style={{ marginBottom: '480px' }}>
      <Table
        columns={columns} 
        dataSource={data} 
        pagination={false} 
        className='small-table'
        rowSelection={{
          selectedRowKeys: alreadySelectedRows,
          onChange: (keys) => {
            setAlreadySelectedRows(keys)
          },
          onSelect: (record) => {
            console.log({record})
          },
          hideSelectAll: true
        }}
      />
    </Card>
  )
}
