import React from 'react'
import { Input, DatePicker } from 'antd';
import DownloadLightBlue from '../../../../../../../components/shared-components/Buttons/DownloadLightBlue';
import moment from 'moment';

const { TextArea } = Input;
const dateFormat = 'YYYY/MM/DD';

export default function Item1() {
  return (
    <>
      <div>
        <label> </label>
        <TextArea
          className='grey'
          autoSize
          value={'                                 2991/59'}
        />
      </div>

      <div>
        <label> </label>
        <TextArea 
          className='white'
          autoSize
          value={'                 '}
        />
      </div>

      <div>
        <label>   </label>
        <TextArea 
          className='white'
          autoSize
          value={'                 '}
        />
      </div>

      <div className='row'>
        <div className="col-3">
          <label>  </label>
          <DatePicker 
            placeholder=' ' 
            className='inline-input w-100 mx-0'
            value={moment('2015/01/01', dateFormat)} format={dateFormat}
          />
        </div>
        <div className="col-3">
          <label></label>
          <Input
            value={' '}
            className='inline-input mx-0' 
          />
        </div>
        <div className="col-3">
          <label>  </label>
          <DownloadLightBlue classn={'w-100'} />
        </div>
      </div>
    </>
  )
}
